	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨电视里放电影《轮回》，是一部探索影片，前一段好像有一些批评，因而有兴趣看。是一个虚构的故事，在现实生活中大概不会发生。但是，反映了社会的一种新的文化心态，就是一些人在社会大变化的过程中茫然了，不知生活的趣味了。同时比较强调一种感觉，生活和爱情均在感觉之中，感觉好就是生活，所以有一个舞蹈演员会对一位无所事事的人感觉好。现实生活中大概有，但不会是一种普遍的现象。今天在商品经济的文化下面，谈感觉的人越来越少，而谈实利的人越来越多。有时是人们不知应该谈什么，别人会谈什么。大概正直的人总是有的，但是谁是呢?</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0069.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0069.md","frontmatter":{"title":"4月6日 星期三"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0069.md absPath of file >>> MarkdownRemark"}}

